import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanFileServiceSnapshotService} from "@generated/vsan-file-service-snapshot-service";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileShareSnapshotConfig} from "@generated/vsan-file-share-snapshot-config";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
} from "@angular/forms";
import {VsanFileShareUtils} from "../file-share-util";
import {FileServiceCreateSnapshotModalContext} from "./create-snapshot-modal-context.data";
import {ReactiveFormUtil} from "@util/reactive-form-util";

@Component({
   selector: "vsan-file-service-create-snapshot",
   templateUrl: "create-snapshot.html",
   styleUrls: ["create-snapshot.scss"],
})
export class CreateSnapshotComponent implements OnInit {
   private readonly MAX_NAME_LENGTH: number = 100;

   private shareUuid: string;
   private existingSnapshots: string[];
   private clusterRef: ManagedObject;

   SNAPSHOT_NAME_CONTROL_NAME: string = "name";
   nameValidateError: string;
   processing: boolean;
   errorMessage: string[];

   snapshotConfigGroup: FormGroup;

   constructor(private snapshotService: VsanFileServiceSnapshotService, private formBuilder: FormBuilder) {}

   ngOnInit(): void {
      const context: FileServiceCreateSnapshotModalContext =
            VsanUiUtils.getModalContext() as FileServiceCreateSnapshotModalContext;
      this.shareUuid = context.share.uuid;
      this.existingSnapshots = context.existingSnapshots;

      this.clusterRef = ManagedObject.contextObject;

      this.snapshotConfigGroup = this.formBuilder.group({
         name: new FormControl("", [this.nameValidator]),
      });
   }

   nameValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      if (!control.touched) {
         return;
      }
      this.nameValidateError = this.validateName(control.value);
      if (this.nameValidateError) {
         return {nameInvalid: {value: control.value}};
      }
      return null;
   };

   validateName(name: string): string {
      if (VsanUiUtils.isEmpty(name.trim())) {
         return VsanUiUtils.getString("vsan.fileservices.snapshot.create.dialog.name.empty");
      } else if (VsanUiUtils.getUtf8Length(name) > this.MAX_NAME_LENGTH) {
         return VsanUiUtils.getString("vsan.fileservices.snapshot.create.dialog.name.max.length.exceed");
      } else if (VsanFileShareUtils.INVALID_CHARS.test(name)) {
         return VsanUiUtils.getString("vsan.fileservices.snapshot.create.dialog.name.invalid.chars");
      } else if (this.existingSnapshots.indexOf(name) !== -1) {
         return VsanUiUtils.getString("vsan.fileservices.snapshot.create.dialog.name.existing");
      }
      return null;
   }

   get hasErrors(): boolean {
      return !VsanUiUtils.isEmpty(this.errorMessage) || !VsanUiUtils.isEmpty(this.nameValidateError);
   }

   ok = async () => {
      ReactiveFormUtil.validateAllFormFields(this.snapshotConfigGroup);
      if (!VsanUiUtils.isEmpty(this.nameValidateError)) {
         return;
      }
      try {
         this.processing = true;
         const configSpec: VsanFileShareSnapshotConfig = VsanFileShareSnapshotConfig.Factory.create(
               this.snapshotConfigGroup.get(this.SNAPSHOT_NAME_CONTROL_NAME).value, this.shareUuid);
         const task: ManagedObject = await this.snapshotService.createShareSnapshot(this.clusterRef, configSpec);
         VsanUiUtils.closeModalDialog(task);
      } catch (err) {
         this.errorMessage = [err];
      } finally {
         this.processing = false;
      }
   };

   cancel = () => {
      VsanUiUtils.closeModalDialog();
   };
}
