import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ClrDatagridPagination, ClrDatagridStateInterface} from "@clr/angular";
import {LabelCellService} from "@component/cell/label-cell.service";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";
import {PipeFilter} from "@component/datagrid/filter/pipe-filter";
import {PaneType} from "@component/master-detail/slide-panel.component";
import {VirtualObjectsTypeUtil} from "@component/virtual-objects-table/virtual-objects-type.util";
import {AffinitySiteLocation} from "@generated/affinity-site-location";
import {DisplayObjectType} from "@generated/display-object-type";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {VirtualObjectType} from "@generated/virtual-object-type";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanFileServiceShareSize} from "@generated/vsan-file-service-share-size";
import {VsanFileShareOwnerType} from "@generated/vsan-file-share-owner-type";
import {VsanFileShareProtocol} from "@generated/vsan-file-share-protocol";
import {VirtualObjectsContext} from "@navigation/context/virtual-objects-context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {FileSizePipe} from "@pipe/FileSizePipe";
import {Icon} from "@util/icon";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanUiUtils} from "@util/vsan-util";
import {Observable} from "rxjs";
import {ServiceUtils} from "../../services/service.utils";
import {FileServiceUtil} from "../file-service-util";
import {VsanFileShareNameComparator} from "./comparators/name-comparator";
import {VsanFileShareOwnerTypeComparator} from "./comparators/owner-type-comparator";
import {VsanFileSharePolicyComparator} from "./comparators/policy-comparator";
import {VsanFileShareProtocolComparator} from "./comparators/protocol-comparator";
import {VsanFileShareQuotaComparator} from "./comparators/quota-comparator";
import {VsanFileShareUsageComparator} from "./comparators/usage-comparator";
import {ShareDetailsTab} from "./detail/share-details-tab";
import {VsanFileShareUtils} from "./file-share-util";
import {OwnerTypeFilter} from "./filters/owner-type-filter";
import {PolicyFilter} from "./filters/policy-filter";

@Component({
   templateUrl: "shares-list.html",
   selector: "vsan-file-shares-list",
   styleUrls: ["shares-list.scss"],
   providers: [LabelCellService, ReferenceWatcher],
})
export class FileSharesListComponent {
   Icon: typeof Icon = Icon;
   readonly DEFAULT_ITEMS_PER_PAGE: number = 20;
   readonly DEFAULT_ITEMS_PER_PAGE_NO_PAGINATION: number = 50;
   readonly DETAIL_PANEL_WIDTH_PERCENT: number = 75;

   private _policies: StoragePolicyData[];

   @Input()
   set policies(policies: StoragePolicyData[]) {
      this._policies = policies;
      this.policyFilter = new PolicyFilter(this.policies);
      this.policyComparator = new VsanFileSharePolicyComparator(this.policies);
   }

   get policies(): StoragePolicyData[] {
      return this._policies;
   }

   @Input()
   fileShares: VsanFileServiceShare[];

   @Input()
   loading: boolean;

   @Input()
   totalCount: number;

   @Input()
   paginationSupported: boolean;

   @Input()
   smbProtocolSupported: boolean;

   @Input()
   smbProtocolLayerPerformanceSupported: boolean;

   @Input()
   smbAbeSupported: boolean;

   @Input()
   shareConfigs: VsanFileServiceShare[];

   @Input()
   hasReadPolicyPermission: boolean;

   @Input()
   hasEditPermission: boolean;

   @Input()
   hostPropertiesMap: Map<string, any>;

   @Input()
   availableDomains: DomainOrHostData[];

   @Input()
   isStretchedCluster: boolean;

   @Input()
   isSnapshotSupported: boolean;

   @Input()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @Input()
   isAffinitySiteSupported: boolean;

   private _selectedItems: VsanFileServiceShare[];

   @Input()
   set selectedItems(val: VsanFileServiceShare[]) {
      this._selectedItems = val || [];
      this.selectedItemsChange.emit(val);
      this.showDetailsForPreselectedItem();
   }

   get selectedItems(): VsanFileServiceShare[] {
      return this._selectedItems;
   }

   private _showPreselectedItemDetails: boolean;

   @Input()
   set showPreselectedItemDetails(value: boolean) {
      this._showPreselectedItemDetails = value;
      this.showDetailsForPreselectedItem();
   }

   @Input()
   set showSnapshots(event: Observable<void>) {
      this.refWatcher.watchSubscriptionRef(event.subscribe(() => {
         if (!this.selectedItems || this.selectedItems.length !== 1) {
            return;
         }
         this.openDetailsTab(this.selectedItems[0], ShareDetailsTab.SNAPSHOTS);
      }));
   }

   @Input()
   set showFileAnalytics(event: Observable<void>) {
      this.refWatcher.watchSubscriptionRef(event.subscribe((s) => {
         if (!this.selectedItems || this.selectedItems.length !== 1) {
            return;
         }
         this.openDetailsTab(this.selectedItems[0], ShareDetailsTab.FILE_ANALYTICS);
      }));
   }

   @Output()
   selectedItemsChange: EventEmitter<VsanFileServiceShare[]> = new EventEmitter();

   @Output()
   shareDataRefresh: EventEmitter<ClrDatagridStateInterface> = new EventEmitter<ClrDatagridStateInterface>(true);

   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   detailItem: VsanFileServiceShare;
   preselectTab: ShareDetailsTab = ShareDetailsTab.BASIC;

   nameFilter: ObjectFilter<any> = new ObjectFilter<any>(VsanFileShareUtils.NAME_FIELD);
   labelsFilter: ObjectFilter<any> = new ObjectFilter<any>(VsanFileShareUtils.LABELS_FIELD);
   quotaFilter: ObjectFilter<any> = new ObjectFilter<any>(VsanFileShareUtils.QUOTA_FIELD);
   softQuotaFilter: ObjectFilter<any> = new ObjectFilter<any>(VsanFileShareUtils.SOFT_QUOTA_FIELD);
   usedCapacityFilter: PipeFilter<any> =
         new PipeFilter<any>(new FileSizePipe(), VsanFileShareUtils.USED_CAPACITY_FIELD);

   ownerTypeFilter: OwnerTypeFilter = new OwnerTypeFilter();
   policyFilter: PolicyFilter = null;

   nameComparator: VsanFileShareNameComparator = new VsanFileShareNameComparator();
   quotaComparator: VsanFileShareQuotaComparator =
         new VsanFileShareQuotaComparator(VsanFileShareUtils.QUOTA_FIELD, VsanFileShareUtils.QUOTA_SIZE_FIELD);
   softQuotaComparator: VsanFileShareQuotaComparator =
         new VsanFileShareQuotaComparator(
               VsanFileShareUtils.SOFT_QUOTA_FIELD,
               VsanFileShareUtils.SOFT_QUOTA_SIZE_FIELD);
   protocolComparator: VsanFileShareProtocolComparator = new VsanFileShareProtocolComparator();
   policyComparator: VsanFileSharePolicyComparator = null;
   usageComparator: VsanFileShareUsageComparator =
         new VsanFileShareUsageComparator(VsanFileShareUtils.USAGE_OVER_QUOTA_FIELD);
   capacityComparator: VsanFileShareUsageComparator =
         new VsanFileShareUsageComparator(VsanFileShareUtils.USED_CAPACITY_FIELD);
   ownerTypeComparator: VsanFileShareOwnerTypeComparator = new VsanFileShareOwnerTypeComparator();

   private showDetailPanel: boolean;

   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   VsanFileShareUtils: typeof VsanFileShareUtils = VsanFileShareUtils;
   VsanFileShareProtocol: typeof VsanFileShareProtocol = VsanFileShareProtocol;
   ServiceUtils: typeof ServiceUtils = ServiceUtils;

   constructor(private navigationService: NavigationService, private refWatcher: ReferenceWatcher) {
   }

   refresh(state: ClrDatagridStateInterface): void {
      this.shareDataRefresh.emit(state);
   }

   isSelected(item: VsanFileServiceShare): boolean {
      return this.detailItem === item && this.showDetailPanel;
   }

   get activePane(): PaneType {
      return this.showDetailPanel ? PaneType.RIGHT : PaneType.LEFT;
   }

   set activePane(pane: PaneType) {
      this.showDetailPanel = pane === PaneType.RIGHT;
   }

   toggle(item: VsanFileServiceShare): void {
      if (this.detailItem === item) {
         this.showDetailPanel = !this.showDetailPanel;
      } else {
         this.openDetailsTab(item);
      }
   }

   openDetailsTab(item: VsanFileServiceShare, tab: ShareDetailsTab = this.preselectTab): void {
      this.detailItem = item;
      this.preselectTab = tab;
      this.activePane = PaneType.RIGHT;
   }

   navigateToVirtualObjects(share: VsanFileServiceShare): void {
      const filter: VirtualObjectType = share.managedBy === VsanFileShareOwnerType.CNS ?
            VirtualObjectsTypeUtil.getVirtualObjectTypeFilter(DisplayObjectType.FILE_VOLUME)
            : VirtualObjectsTypeUtil.getVirtualObjectTypeFilter(DisplayObjectType.FILE_SHARE);
      const virtualObjectsContext: VirtualObjectsContext = {
         presentVirtualObjectUuids: share.objectUuids,
         filterTypes: [filter],
      };
      this.navigationService.navigateTo(PlatformExtension.VIRTUAL_OBJECTS, null, virtualObjectsContext);
   }

   navigateToHost(share: VsanFileServiceShare): void {
      this.navigationService.navigateTo(
            PlatformExtension.HOST_SUMMARY,
            this.hostPropertiesMap.get(share.host).nodeRef);
   }

   getPolicyName(policyId: string): string {
      let policy: StoragePolicyData = null;

      if (this.policies) {
         policy = this.policies.find(p => p.id === policyId);
      }

      return policy ? policy.name : null;
   }

   getQuotaLabel(quota: number, quotaSize: VsanFileServiceShareSize): string {
      return VsanUiUtils.getString("vsan.fileservices.shares.quota",
            VsanUiUtils.localizeNumber(quota),
            VsanFileShareUtils.getSizeLabel(quotaSize));
   }

   getOwnerTypeLabel(managedBy: VsanFileShareOwnerType): string {
      return VsanFileShareUtils.getShareOwnerType(managedBy);
   }

   getProtocolLabel(protocol: VsanFileShareProtocol): string {
      return VsanFileShareUtils.getShareProtocolLabel(protocol);
   }

   getHostNameWithDomain(share: VsanFileServiceShare): string {
      const site: DomainOrHostData = this.findDomainByShare(share);
      return site?.isPreferred
            ? VsanUiUtils.getString("vsan.fileservices.affinitySite.preferred.label", share.host)
            : VsanUiUtils.getString("vsan.fileservices.affinitySite.nonPreferred.label", share.host);
   }

   getAffinityLocationStateIconShape(share: VsanFileServiceShare): string {
      return this.isShareInExpectedDomain(share)
            ? Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE
            : Icon.CLR_WARN_STANDARD_ICON_SHAPE;
   }

   getAffinityLocationStateIconClass(share: VsanFileServiceShare): string {
      return Icon.clrIconShapeClassMap[this.getAffinityLocationStateIconShape(share)];
   }

   getAffinityLocationStateIconTitle(share: VsanFileServiceShare): string {
      return this.isShareInExpectedDomain(share)
            ? this.getAffinitySite(share.config.affinityLocation)
            : VsanUiUtils.getString("vsan.fileservices.shares.col.affinity.mismatch");
   }

   getAffinitySite(site: AffinitySiteLocation): string {
      return FileServiceUtil.getAffinitySiteLabel(site, this.availableDomains);
   }

   get defaultPageSize(): number {
      return this.paginationSupported
            ? this.DEFAULT_ITEMS_PER_PAGE
            : this.DEFAULT_ITEMS_PER_PAGE_NO_PAGINATION;
   }

   handleError(error: string): void {
      this.onError.emit(error);
   }

   private showDetailsForPreselectedItem(): void {
      if (!this._showPreselectedItemDetails || !VsanUiUtils.isSingleElement(this.selectedItems)) {
         return;
      }

      this.openDetailsTab(this.selectedItems[0]);
      this._showPreselectedItemDetails = false;
   }

   getPaginationLabel(pagination: ClrDatagridPagination): string {
      return VsanUiUtils.getDatagridPaginatedFooterLabel(pagination.totalItems, pagination.firstItem,
            pagination.lastItem, this.defaultPageSize,
            "vsan.fileservices.shares.grid.fileshare",
            "vsan.fileservices.shares.grid.fileshares",
            "vsan.fileservices.shares.grid.page");
   }

   private isShareInExpectedDomain(share: VsanFileServiceShare): boolean {
      // The share can be on any domain, both preferred & secondary are expected
      if (share.config.affinityLocation === AffinitySiteLocation.NONE) {
         return true;
      }
      const site: DomainOrHostData = this.findDomainByShare(share);
      if (!site) {
         return false;
      }
      if (share.config.affinityLocation === AffinitySiteLocation.PREFERRED_DOMAIN) {
         // The share is expected to be on preferred domain, we have to check if the site is preferred site
         return site.isPreferred;
      }
      return !site.isPreferred;
   }

   private findDomainByShare(share: VsanFileServiceShare): DomainOrHostData {
      return this.availableDomains.find(domain => domain.children.some(child => child.label === share.host));
   }
}
