import {EventEmitter} from "@angular/core";
import {ClrDatagridStateInterface} from "@clr/angular";
import {Subject} from "rxjs";
import {debounceTime, switchMap} from "rxjs/operators";
import {VsanFileSharesService} from "@generated/vsan-file-shares-service";
import {FileSharesPaginationResult} from "@generated/file-shares-pagination-result";
import {FileSharesPaginationSpec} from "@generated/file-shares-pagination-spec";
import {FileShareFilter} from "@generated/file-share-filter";
import {ManagedObject} from "@service/managed-object";
import {VsanFileShareQueryProperties} from "@generated/vsan-file-share-query-properties";
import {VsanUiUtils} from "@util/vsan-util";

export interface IFileShareLoader {
   load(state: ClrDatagridStateInterface, managedBy?: string): Promise<FileSharesPaginationResult>;
}

/**
 * File share loader used when pagination is supported
 */
export class FileShareLoader implements IFileShareLoader {
   private debouncer: Subject<any> = new Subject<any>();
   private loadComplete: EventEmitter<FileSharesPaginationResult> = new EventEmitter<FileSharesPaginationResult>();
   private shareType: string;

   constructor(protected domainName: string,
         protected clusterRef: ManagedObject,
         protected fileShareService: VsanFileSharesService) {
      this.debouncer.asObservable()
            .pipe(debounceTime(500)) // delay 0.5s to wait for a completed user input
            .pipe(switchMap(state => this.loadShares(state)))
            .subscribe(result => this.loadComplete.emit(result), error => this.loadComplete.error(error));
   }

   load(state: ClrDatagridStateInterface, shareType: string): Promise<FileSharesPaginationResult> {
      this.shareType = shareType;
      this.debouncer.next(state);

      return new Promise<FileSharesPaginationResult>((accept, reject) => {
         this.loadComplete.asObservable().subscribe(accept, reject);
      });
   }

   protected loadShares = async (state: ClrDatagridStateInterface, paginationSupported: boolean = true) => {
      return await this.fileShareService
            .filterShares(this.clusterRef, this.prepareQuerySpec(state), paginationSupported);
   };

   protected prepareQuerySpec = (state: ClrDatagridStateInterface): FileSharesPaginationSpec => {
      const filters: FileShareFilter[] = VsanUiUtils.isEmpty(state.filters)
            ? []
            : state.filters.map(filter => this.getFileShareFilter(filter));
      return FileSharesPaginationSpec.Factory.create(
            state.page.size,
            state.page.current,
            this.domainName,
            this.shareType,
            filters,
            VsanFileShareQueryProperties.Factory.create(true, true, true, true));
   };

   private getFileShareFilter(filter: any): FileShareFilter {
      const filterValues: any[] = filter.selectedFilterValues || [];
      return FileShareFilter.Factory.create(filter.vsanDgField, filterValues.map(value => value.toString()));
   }
}
