import {Component, ElementRef, HostListener, Input, ViewChild} from "@angular/core";
import {ClrDatagridFilter, ClrDatagridFilterInterface} from "@clr/angular";
import {Subject} from "rxjs";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanUiUtils} from "@util/vsan-util";

@Component ({
   selector: "vsan-file-share-name-filter",
   templateUrl: "file-share-name-filter.html",
   styleUrls: ["file-share-name-filter.scss"],
})
export class ShareNameFilterComponent implements ClrDatagridFilterInterface<any> {
   @Input()
   private allShares: VsanFileServiceShare[];

   @ViewChild("fileShareDropdownElement")
   private fileShareDropdownElement: ElementRef;

   // This is for generating the share query spec when the filter is activated
   vsanDgField: string = "config.name";

   selectedFilterValues: string[];

   isDropdownOpened: boolean;

   matchedShares: VsanFileServiceShare[];

   private _search: string;

   set search(val: string) {
      this._search = val;
      if (VsanUiUtils.isEmpty(val)) {
         this.matchedShares = [];
         if (!VsanUiUtils.isEmpty(this.selectedFilterValues)) {
            this.startFilter();
         }
         return;
      }
      if (VsanUiUtils.isEmpty(this.allShares)) {
         this.isDropdownOpened = false;
         return;
      }
      this.openDropdown();
   }

   get search(): string {
      return this._search;
   }

   // overrides parent
   changes: Subject<string[]> = new Subject<string[]>();

   constructor(private filterContainer: ClrDatagridFilter) {
      filterContainer.setFilter(this);
   }

   private openDropdown(): void {
      this.matchedShares = this.allShares.filter(share =>
            share.config.name.toLowerCase().indexOf(this.search.trim().toLowerCase()) > -1);
      this.isDropdownOpened = true;
   }

   isActive(): boolean {
      return !VsanUiUtils.isEmpty(this.search);
   }

   // overrides parent
   accepts(item: VsanFileServiceShare): boolean {
      return item.config.name.toLowerCase().includes(this.search.trim());
   }

   selectShare(share: VsanFileServiceShare): void {
      this.search = share.config.name;
      this.isDropdownOpened = false;
      this.matchedShares = [share];
      this.startFilter();
   }

   startFilter(): void {
      this.selectedFilterValues = [];
      if (!VsanUiUtils.isEmpty(this.matchedShares)) {
         this.selectedFilterValues = this.matchedShares.map(share => share.config.name);
      }
      this.changes.next(this.selectedFilterValues);
   }

   @HostListener("click", ["$event"])
   clickout(event: Event): void {
      // close the dropdown if user clicks outside of the dropdown element
      if (this.isDropdownOpened &&
            !this.fileShareDropdownElement.nativeElement.contains(event.target)) {
         this.isDropdownOpened = false;
      }
   }
}
