import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {ShareDetailsTab} from "./share-details-tab";
import {ManagedObject} from "@service/managed-object";

@Component({
   selector: "vsan-file-share-detail",
   templateUrl: "share-detail.html",
   styleUrls: ["share-detail.scss"],
})
export class ShareDetailComponent {

   @Input()
   share: VsanFileServiceShare;

   @Input()
   policies: StoragePolicyData[];

   @Input()
   hostPropertiesMap: Map<string, any>;

   @Input()
   smbProtocolSupported: boolean;

   @Input()
   smbProtocolLayerPerformanceSupported: boolean;

   @Input()
   availableDomains: DomainOrHostData[];

   @Input()
   affinitySiteAvailable: boolean;

   @Input()
   smbAbeSupported: boolean;

   @Input()
   snapshotAvailable: boolean;

   @Input()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @Input()
   hasEditPermission: boolean;

   @Input()
   set activeTab(tab: ShareDetailsTab) {
      this.resetTabs();
      this.openTab(tab);
   }

   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   clusterRef: ManagedObject = ManagedObject.contextObject;

   basicTabActive: boolean;
   analyticsTabActive: boolean;
   placementTabActive: boolean;
   perfTabActive: boolean;
   snapshotsTabActive: boolean;

   get showFileAnalyticsDetails(): boolean {
      return this.fileServiceConfig
            && this.fileServiceConfig.isFileAnalyticsEnabled;
   }

   private resetTabs(): void {
      this.basicTabActive = false;
      this.analyticsTabActive = false;
      this.placementTabActive = false;
      this.perfTabActive = false;
      this.snapshotsTabActive = false;
   }

   private openTab(tab: ShareDetailsTab): void {
      if (tab === null || tab === undefined) {
         this.basicTabActive = true;
         return;
      }
      switch (tab) {
         case ShareDetailsTab.BASIC:
            this.basicTabActive = true;
            break;
         case ShareDetailsTab.FILE_ANALYTICS:
            this.analyticsTabActive = true;
            break;
         case ShareDetailsTab.PERFORMANCE:
            this.perfTabActive = true;
            break;
         case ShareDetailsTab.PHYSICAL_PLACEMENT:
            this.placementTabActive = true;
            break;
         case ShareDetailsTab.SNAPSHOTS:
            this.snapshotsTabActive = true;
            break;
      }
   }
}
