import {ClrDatagridStateInterface} from "@clr/angular";
import {FileShareLoader} from "./file-share-loader";
import {FileSharesPaginationResult} from "@generated/file-shares-pagination-result";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanUiUtils} from "@util/vsan-util";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";
import {BaseComparator} from "@component/datagrid/comparator/base-comparator";
import {FileSharesPaginationSpec} from "@generated/file-shares-pagination-spec";
import {FileShareFilter} from "@generated/file-share-filter";
import {VsanFileShareQueryProperties} from "@generated/vsan-file-share-query-properties";

/**
 * File share loader used when pagination is NOT supported
 */
export class FileShareLegacyLoader extends FileShareLoader {
   private cachedPaginationResult: FileSharesPaginationResult;

   load(state: ClrDatagridStateInterface): Promise<FileSharesPaginationResult> {
      return new Promise<FileSharesPaginationResult>((accept, reject) => {
         if (this.cachedPaginationResult) {
            const clonedResult: FileSharesPaginationResult = JSON.parse(JSON.stringify(this.cachedPaginationResult));
            this.clientSideFilterAndSort(state, clonedResult);
            accept(clonedResult);
            return;
         }
         this.loadShares(state, false)
               .then(result => {
                  this.cachedPaginationResult = result;
                  accept(result);
               })
               .catch(reject);
      });
   }

   prepareQuerySpec = (state: ClrDatagridStateInterface): FileSharesPaginationSpec => {
      return FileSharesPaginationSpec.Factory.create(
            state.page.size,
            null,
            this.domainName,
            null,
            null,
            null);
   };

   /**
    * Do browser side filter and sort, only works when pagination is not supported
    * @param state
    * @param paginationResult
    */
   private clientSideFilterAndSort(state: ClrDatagridStateInterface, paginationResult: FileSharesPaginationResult) {
      if (VsanUiUtils.isEmpty(this.cachedPaginationResult.shares)) {
         return;
      }
      if (state.filters) {
         paginationResult.shares = this.cachedPaginationResult.shares.filter(share =>
               state.filters.every(filter => (filter.filterFn as ObjectFilter<VsanFileServiceShare>)
                     .accepts(share, filter.value)));
      } else {
         paginationResult.shares = this.cachedPaginationResult.shares;
      }
      paginationResult.total = paginationResult.shares.length;

      if (state.sort) {
         paginationResult.shares.sort(
               (a: VsanFileServiceShare, b: VsanFileServiceShare) =>
                     (state.sort.by as BaseComparator<VsanFileServiceShare>).compare(a, b));
         if (state.sort.reverse) {
            paginationResult.shares.reverse();
         }
      }
   }
}
