import {Component, Input, OnChanges} from "@angular/core";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {FileAnalyticsScanStatusInfo} from "@generated/file-analytics-scan-status-info";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {EditShareFileAnalyticsContext} from "../../edit-share-file-analytics/edit-share-file-analytics-context";
import {ManagedObject} from "@service/managed-object";

@Component({
   selector: "file-share-analytics-details",
   templateUrl: "share-analytics-details.html",
   styleUrls: ["share-analytics-details.scss"],
})
export class ShareAnalyticsDetailsComponent implements OnChanges {
   @Input()
   share: VsanFileServiceShare;

   @Input()
   perfServiceEnabled: boolean;

   @Input()
   hasEditPermission: boolean;

   fileShares: VsanFileServiceShare[] = [];
   isFileAnalyticsEnabled: boolean;
   shareContext: EditShareFileAnalyticsContext;

   scanStatus: FileAnalyticsScanStatusInfo;
   loading: boolean;
   errorMessage: string;

   UnavailableViewSpec: typeof UnavailableViewSpec = UnavailableViewSpec;

   ngOnChanges(): void {
      if (!this.share) {
         return;
      }
      this.isFileAnalyticsEnabled = this.share.config.isFileAnalyticsEnabled;
      this.updateShareContext();
      if (!this.isFileAnalyticsEnabled) {
         this.scanStatus = null;
      }
      if (this.perfServiceEnabled && this.isFileAnalyticsEnabled && !this.scanStatus) {
         this.loading = true;
      }
      this.fileShares = [this.share];
   }

   update(scanStatus: FileAnalyticsScanStatusInfo): void {
      this.scanStatus = scanStatus;
      if (this.scanStatus) {
         this.fileShares = [this.share];
      }
      this.loading = false;
   }

   private updateShareContext(): void {
      this.shareContext = {
         clusterRef: ManagedObject.contextObject,
         share: this.share.config,
         shareUuid: this.share.uuid,
         isSmbAbeSupported: false,
      };
   }
}
