import {BaseComparator} from "@component/datagrid/comparator/base-comparator";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanFileShareUtils} from "../file-share-util";

/**
 * Comparator for file share policy
 */
export class VsanFileSharePolicyComparator extends BaseComparator<VsanFileServiceShare> {
   constructor(private policies: StoragePolicyData[]) {
      super();
   }

   protected compareValue(a: VsanFileServiceShare, b: VsanFileServiceShare): number {
      const dataField: string = VsanFileShareUtils.POLICY_FIELD;
      const policy1: StoragePolicyData = this.policies.find(policy => policy.id === this.stringify(a, dataField));
      const policyName1: string = policy1 && policy1.name ? policy1.name : VsanUiUtils.getString("vsan.na.label");

      const policy2: StoragePolicyData = this.policies.find(policy => policy.id === this.stringify(b, dataField));
      const policyName2: string = policy2 && policy2.name ? policy2.name : VsanUiUtils.getString("vsan.na.label");
      return policyName1.localeCompare(policyName2);
   }
}
