import {Component, ElementRef, EventEmitter ,Input, Output, ViewChild} from "@angular/core";

/**
 * Use this component to show a tooltip message after copying sth with ngx-clipboard
 */
@Component({
   selector: "vsan-balloon-tooltip",
   templateUrl: "./balloon-tooltip.html",
   styleUrls: ["balloon-tooltip.scss"]
})
export class BalloonTooltipComponent {
   private _tooltipElement: ElementRef;

   @ViewChild("vsan_balloon_tooltip")
   set tooltipElement(element: ElementRef) {
      if (!element) {
         return;
      }
      this._tooltipElement = element;
      this.focusTooltip();
   }

   get tooltipElement(): ElementRef {
      return this._tooltipElement;
   }

   @Input()
   visible: boolean;

   @Input()
   isWide: boolean;

   @Output()
   visibleChange: EventEmitter<boolean> = new EventEmitter<boolean>();

   hideBalloonTooltip = () => {
      this.visible = false;
      this.visibleChange.emit(this.visible);
   };

   private focusTooltip = () => {
      const tooltipDiv: HTMLElement = this.tooltipElement.nativeElement;
      // Set the focus to the div element, and later we can hide the balloon with the onblur event
      setTimeout(() => {
         tooltipDiv.focus();
      });
   };
}
