import {BaseComparator} from "@component/datagrid/comparator/base-comparator";
import {VsanFileShareUtils} from "../file-share-util";
import {VsanFileShareOwnerType} from "@generated/vsan-file-share-owner-type";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";

/**
 * Comparator for file share owner type
 */
export class VsanFileShareOwnerTypeComparator extends BaseComparator<VsanFileServiceShare> {
   protected compareValue(a: VsanFileServiceShare, b: VsanFileServiceShare): number {
      const dataField: string = VsanFileShareUtils.OWNER_TYPE_FIELD;
      const ownerTypeText1: string = this.stringify(a, dataField);
      const ownerTypeText2: string = this.stringify(b, dataField);
      const ownerType1: string = VsanFileShareUtils.getShareOwnerType(ownerTypeText1 as VsanFileShareOwnerType);
      const ownerType2: string = VsanFileShareUtils.getShareOwnerType(ownerTypeText2 as VsanFileShareOwnerType);
      return ownerType1.localeCompare(ownerType2);
   }
}
