import {StoragePolicyData} from "@generated/storage-policy-data";
import {Reflection} from "@util/reflection";
import {VsanUiUtils} from '@util/vsan-util';
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanFileShareUtils} from "../file-share-util";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";

/**
 * Extends BaseFilter by allowing the consumer to filter the hierarchical data field.
 * It tries to find the final string value of the data field by visiting the attribute hirarchy.
 */
export class PolicyFilter extends ObjectFilter<VsanFileServiceShare> {
   constructor(private policies: StoragePolicyData[]) {
      super(VsanFileShareUtils.POLICY_FIELD);
   }

   protected stringify(data: VsanFileServiceShare): string  {
      if (!data) {
         return "";
      }
      const value: any = Reflection.getFieldValue(data, this.dataField);
      const policy: StoragePolicyData = this.policies.find(p => p.id === value);
      return policy && policy.name ? policy.name : VsanUiUtils.getString("vsan.na.label");
   }
}
