import {BaseComparator} from "@component/datagrid/comparator/base-comparator";
import {VsanFileShareUtils} from "../file-share-util";
import {VsanFileShareSnapshot} from "@generated/vsan-file-share-snapshot";

/**
 * Comparator for file share snapshot name
 */
export class VsanFileShareSnapshotNameComparator extends BaseComparator<VsanFileShareSnapshot> {
   protected compareValue(a: VsanFileShareSnapshot, b: VsanFileShareSnapshot): number {
      const dataField = VsanFileShareUtils.NAME_FIELD;
      return this.stringify(a, dataField).localeCompare(this.stringify(b, dataField));
   }
}
