import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {RefreshHandler} from "@service/global-refresh.service";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {VsanFileServiceDomain} from "@generated/vsan-file-service-domain";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {FileServicesCreateShareWizardModalContext} from "../create-share/create-share-modal-context.data";
import {FileServicesDeleteShareWizardModalContext} from "../delete-share/delete-share-modal-context.data";
import {PbmDataProvider} from "@generated/pbm-data-provider";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {TaskMonitorService} from "@service/task-monitor-service";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileServiceHostIpSettings} from "@generated/vsan-file-service-host-ip-settings";
import {SharesContext} from "@navigation/context/shares-context";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanFileShareOwnerType} from "@generated/vsan-file-share-owner-type";
import {VsanFileShareProtocol} from "@generated/vsan-file-share-protocol";
import {FileSharesPaginationResult} from "@generated/file-shares-pagination-result";
import {ClrDatagridStateInterface} from "@clr/angular";
import {FileShareLoaderFactory} from "./loader/file-share-loader-factory";
import {IFileShareLoader} from "./loader/file-share-loader";
import {VsanFileShareUtils} from "./file-share-util";
import {SelectOption} from "../select-option";
import {ComputeInventoryService} from "@generated/compute-inventory-service";
import {InventoryEntryData} from "@generated/inventory-entry-data";
import {FileServiceFeature} from "@generated/file-service-feature";
import {FileShareTypeFilterOptions} from "./filters/file-share-type-filter-options";
import {FilterContextKey} from "@generated/filter-context-key";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {FileServiceUtil} from "../file-service-util";
import {Subject} from "rxjs";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";

@Component({
   templateUrl: "shares.html",
   styleUrls: ["shares.scss"],
   selector: "vsan-file-service-shares",
})
export class FileServicesSharesComponent implements OnInit {
   private static readonly SHARES_LOADER: string = "shares-loader";
   private static readonly FETCH_LOADER: string = "fetch-loader";

   private sharesLoader: IFileShareLoader;

   clusterRef: ManagedObject;

   @LoadingIndicator({name: FileServicesSharesComponent.FETCH_LOADER})
   loading: boolean;

   policies: StoragePolicyData[];
   fileShares: VsanFileServiceShare[];
   shareConfigs: VsanFileServiceShare[]; // This only contains share names, and it is beyond the pagination limit
   totalCount: number = 0;

   @LoadingIndicator({name: FileServicesSharesComponent.SHARES_LOADER})
   loadingShares: boolean;

   selectedShares: VsanFileServiceShare[];
   preselectedShareName: string;

   @ErrorMessage({
      name: [
         FileServicesSharesComponent.FETCH_LOADER, FileServicesSharesComponent.SHARES_LOADER],
   })
   error: string;

   domainConfig: VsanFileServiceDomain;
   hasEditPermission: boolean;
   hasReadPolicyPermission: boolean;
   fileServiceConfig: VsanFileServiceCommonConfig;
   vcCapabilityData: VsanCapabilityData;
   paginationSupported: boolean;
   smbProtocolSupported: boolean;
   smbProtocolLayerPerformanceSupported: boolean;
   kerberosSupported: boolean;
   snapshotSupported: boolean;
   affinitySiteSupported: boolean;
   smbAbeSupported: boolean;

   copiedUrl: string;
   copyUrlTooltipVisible: boolean;

   copyMmcCommandTooltipVisible: boolean;

   VsanFileShareUtils: typeof VsanFileShareUtils = VsanFileShareUtils;
   state: ClrDatagridStateInterface;

   shareTypeFilter: FileShareTypeFilterOptions = FileShareTypeFilterOptions.FILE_SHARE;

   shareTypeFilterOptions: SelectOption<FileShareTypeFilterOptions>[] = [
      {
         value: FileShareTypeFilterOptions.FILE_SHARE,
         label: VsanUiUtils.getString("vsan.fileservices.shares.filter.type.user"),
      } as SelectOption<FileShareTypeFilterOptions>,
      {
         value: FileShareTypeFilterOptions.CNS_VOLUME,
         label: VsanUiUtils.getString("vsan.fileservices.shares.filter.type.cns"),
      } as SelectOption<FileShareTypeFilterOptions>,
      {
         value: FileShareTypeFilterOptions.ALL,
         label: VsanUiUtils.getString("vsan.fileservices.shares.filter.type.all"),
      } as SelectOption<FileShareTypeFilterOptions>,
   ];

   hostPropertiesMap: Map<string, any> = new Map<string, any>();
   availableDomains: DomainOrHostData[];
   isStretchedCluster: boolean;
   showSnapshots: Subject<void> = new Subject<void>();
   showAnalytics: Subject<void> = new Subject<void>();

   constructor(private fileServicesConfigService: VsanFileServiceConfigService,
         private shareLoaderFactory: FileShareLoaderFactory,
         private permissionService: PermissionService,
         private spbmService: PbmDataProvider,
         private taskMonitor: TaskMonitorService,
         private inventoryService: ComputeInventoryService,
         private stretchedClusterService: VsanStretchedClusterService,
         private capabilityProvider: VsanCapabilityProvider) {
   }

   async ngOnInit(): Promise<void> {
      this.clusterRef = ManagedObject.contextObject;
      const context: SharesContext = VsanUiUtils.getNavigationData();
      if (context && context.preselectedShareName) {
         this.preselectedShareName = context.preselectedShareName;
         this.shareTypeFilter = context.shareTypeFilter;
      }
      await this.fetch();
   }

   @RefreshHandler
   @Loader({
      name: FileServicesSharesComponent.FETCH_LOADER,
      srMessage: "vsan.fileservices.shares.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   private async fetch(): Promise<void> {
      await Promise.all([
         this.getFileServiceConfig(),
         this.getEditPermission(),
         this.getReadPolicyPermission(),
         this.getSpbmPolicyPermission(),
         this.getVcCapabilities(),
         this.getHostsData(),
         this.getSupportedRuntimeFeatures(),
         this.getAvailableDomains(),
         this.getIsStretchedCluster(),
      ]);

      if (this.domainConfig) {
         this.sharesLoader = this.shareLoaderFactory.createLoader(
               this.paginationSupported,
               this.domainConfig.name);
      }
   }

   private async getFileServiceConfig(): Promise<void> {
      const config: VsanFileServiceCommonConfig = await this.fileServicesConfigService.getConfig(this.clusterRef);
      this.fileServiceConfig = config;
      if (!!this.fileServiceConfig) {
         this.domainConfig = config.domainConfig;
      }
   }

   private async getEditPermission(): Promise<void> {
      this.hasEditPermission = await this.permissionService
            .hasPermissions(this.clusterRef, [Privileges.CONFIG_STORAGE()]);
   }

   private async getReadPolicyPermission(): Promise<void> {
      this.hasReadPolicyPermission = await this.permissionService
            .hasVcPermissions(ManagedObject.contextObject, [Privileges.READ_POLICIES()]);
   }

   private async getSpbmPolicyPermission(): Promise<void> {
      this.policies = await this.spbmService.getStoragePolicies(this.clusterRef);
   }

   private async getVcCapabilities(): Promise<void> {
      this.vcCapabilityData = await this.capabilityProvider.getVcCapabilityData(this.clusterRef);
   }

   private async getHostsData(): Promise<void> {
      const filterContext: any = {};
      filterContext[FilterContextKey.CONTEXT_REF.toString()] = ManagedObject.contextObject;
      const hostsData: InventoryEntryData[] = await this.inventoryService.getNodeChildren(
            ManagedObject.contextObject, filterContext);
      hostsData.forEach(host => this.hostPropertiesMap.set(host.name, host));
   }

   private async getSupportedRuntimeFeatures(): Promise<void> {
      try {
         const result: any[] = await this.fileServicesConfigService.isFeatureSupportedOnRuntime(
               this.clusterRef,
               [
                  FileServiceFeature.PAGINATION,
                  FileServiceFeature.SMB,
                  FileServiceFeature.KERBEROS,
                  FileServiceFeature.SNAPSHOT,
                  FileServiceFeature.AFFINITY_SITE,
                  FileServiceFeature.SMB_PERFORMANCE,
                  FileServiceFeature.SMB_ABE,
               ]);
         this.paginationSupported = result[FileServiceFeature.PAGINATION];
         this.smbProtocolSupported = result[FileServiceFeature.SMB];
         this.kerberosSupported = result[FileServiceFeature.KERBEROS];
         this.snapshotSupported = result[FileServiceFeature.SNAPSHOT];
         this.affinitySiteSupported = result[FileServiceFeature.AFFINITY_SITE];
         this.smbProtocolLayerPerformanceSupported = result[FileServiceFeature.SMB_PERFORMANCE];
         this.smbAbeSupported = result[FileServiceFeature.SMB_ABE];
         if (this.paginationSupported) {
            this.shareConfigs = await this.fileServicesConfigService.getFileShareAttributes(
                  this.clusterRef, [FileServiceUtil.SHARE_NAME_PROPERTY]);
         }
      } catch (error) {
         Logger.warn("Failed to query file service runtime features. ", error);
         this.paginationSupported = false;
         this.smbProtocolSupported = false;
         this.kerberosSupported = false;
         this.snapshotSupported = false;
         this.affinitySiteSupported = false;
         this.smbProtocolLayerPerformanceSupported = false;
         this.smbAbeSupported = false;
      }
   }

   private async getAvailableDomains(): Promise<void> {
      this.availableDomains = await this.stretchedClusterService.getAvailableDomains(this.clusterRef);
   }

   private async getIsStretchedCluster(): Promise<void> {
      this.isStretchedCluster = await this.stretchedClusterService.getIsVsanStretchedCluster(this.clusterRef);
   }

   @Loader({
      name: FileServicesSharesComponent.SHARES_LOADER,
      srMessage: "vsan.fileservices.shares.title",
      srMessageType: SrMessageType.DATA,
   })
   async loadFileShares(state: ClrDatagridStateInterface): Promise<void> {
      this.state = state;
      const fileSharesPaginatedResult: FileSharesPaginationResult =
            await this.sharesLoader.load(state, this.getTypeFilterValue());
      this.totalCount = fileSharesPaginatedResult.total;
      this.fileShares = fileSharesPaginatedResult.shares;
      // re-select the selected shares
      this.selectShares();
   }

   private getTypeFilterValue = (): string => {
      if (!this.paginationSupported || this.shareTypeFilter === FileShareTypeFilterOptions.ALL) {
         return "";
      }
      return this.shareTypeFilter.toString();
   };

   /**
    * If we have selected share object before fetching the list again,
    * we need to reset them in case the share was updated.
    */
   private selectShares = () => {
      if (!VsanUiUtils.isEmpty(this.selectedShares)) {
         this.selectedShares = this.fileShares
               .filter(share => this.selectedShares.some(item => item.uuid === share.uuid));
      } else if (this.preselectedShareName) {
         this.selectedShares = this.fileShares
               .filter(share => share.config.name === this.preselectedShareName);
      }
   };

   onAdd(): void {
      new ModalBuilder("createFileShare")
            .setDimensions(ModalBuilder.WIZARD_XL)
            .open(this.getShareContext(null))
            .then(this.awaitTask);
   }

   onEdit(): void {
      new ModalBuilder("createFileShare")
            .setDimensions(ModalBuilder.WIZARD_XL)
            .open(this.getShareContext(this.selectedShares[0]))
            .then(this.awaitTask);
   }

   getShareContext(share: VsanFileServiceShare): FileServicesCreateShareWizardModalContext {
      return {
         clusterRef: this.clusterRef,
         share: share ? share.config : null,
         shareUuid: share ? share.uuid : null,
         domain: this.domainConfig,
         policies: this.policies,
         vcCapabilityData: this.vcCapabilityData,
         isFileAnalyticsEnabledOnCluster: this.fileServiceConfig.isFileAnalyticsEnabled,
         isAffinitySiteSupported: this.affinitySiteSupported && this.isStretchedCluster,
         isKerberosSupported: this.kerberosSupported,
         isSmbProtocolSupported: this.smbProtocolSupported,
         isSmbAbeSupported: this.smbAbeSupported,
      };
   }

   onDelete(): void {
      const context: FileServicesDeleteShareWizardModalContext = {
         clusterRef: this.clusterRef,
         domain: this.domainConfig,
         shares: this.selectedShares,
      };
      new ModalBuilder("fileServiceShares/deleteShare")
            .setSize(580, 260)
            .open(context)
            .then((task) => {
               this.selectedShares = [];
               this.awaitTask(task);
            });
   }

   onCopiedSuccess = (event: any) => {
      this.copiedUrl = event.content;
      this.copyUrlTooltipVisible = true;
   };

   private awaitTask = (taskRef: ManagedObject | ManagedObject[]) => {
      if (Array.isArray(taskRef)) {
         Promise.all(taskRef.map(task => this.taskMonitor.getAwaitResult(task)))
               .then(this.fetch.bind(this))
               .catch(err => this.error = err);
         return;
      }
      this.taskMonitor.getAwaitResult(taskRef)
            .then(this.fetch.bind(this))
            .catch(err => this.error = err);
   };

   get defaultIp(): string {
      if (this.domainConfig) {
         const defaultIp: VsanFileServiceHostIpSettings =
               this.domainConfig.ipSettings.find(ipSetting => ipSetting.isDefault);
         if (defaultIp) {
            return defaultIp.address;
         }
      }
      return "";
   }

   areSelectedSharesManagedByUser(): boolean {
      return !VsanUiUtils.isEmpty(this.selectedShares)
            && this.selectedShares.every(share => share.managedBy === VsanFileShareOwnerType.USER);
   }

   isSelectedShareManagedByUser(): boolean {
      return VsanUiUtils.isSingleElement(this.selectedShares)
            && this.selectedShares[0].managedBy === VsanFileShareOwnerType.USER;
   }

   isSmbShareSelected(): boolean {
      return !VsanUiUtils.isEmpty(this.selectedShares)
            && this.selectedShares.length === 1
            && this.selectedShares[0].protocol === VsanFileShareProtocol.SMB;
   }

   get smbShareMmc(): string {
      if (VsanUiUtils.isEmpty(this.selectedShares)) {
         return null;
      }
      return VsanFileShareUtils.getMmcCommand(this.selectedShares[0]);
   }

   getProtocolLabel(protocol: string): string {
      return VsanFileShareUtils.getExportPathMenuLabel(protocol);
   }

   get showPreselectedItemDetails(): boolean {
      return !!this.preselectedShareName;
   }
}
