import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {FileServicesDeleteShareWizardModalContext} from "./delete-share-modal-context.data";
import {VsanFileSharesService} from "@generated/vsan-file-shares-service";
import {VsanFileServiceDomain} from "@generated/vsan-file-service-domain";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";

@Component({
   templateUrl: "delete-share.html",
   selector: "vsan-file-service-delete-share",
   styleUrls: ["delete-share.scss"],
})
export class DeleteShareComponent implements OnInit {

   clusterRef: ManagedObject;
   domain: VsanFileServiceDomain;
   shares: VsanFileServiceShare[];
   errors: string[];
   loading: boolean;

   constructor(private fileServiceShareService: VsanFileSharesService) {
   }

   ngOnInit(): void {
      const context: FileServicesDeleteShareWizardModalContext =
            VsanUiUtils.getModalContext() as FileServicesDeleteShareWizardModalContext;
      this.clusterRef = context.clusterRef;
      this.domain = context.domain;
      this.shares = context.shares;
   }

   async confirm(): Promise<void> {
      this.errors = null;
      const shareUuids: string[] = this.shares.map(share => share.uuid);
      try {
         this.loading = true;
         const taskRefs: ManagedObject[] = await this.fileServiceShareService.deleteShares(this.clusterRef, shareUuids);
         VsanUiUtils.closeModalDialog(taskRefs);
      } catch (error) {
         this.errors  = [error];
      } finally {
         this.loading = false;
      }
   }
}
