import {Component, Input, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanFileServiceSnapshotService} from "@generated/vsan-file-service-snapshot-service";
import {VsanFileShareSnapshotQuerySpec} from "@generated/vsan-file-share-snapshot-query-spec";
import {Logger} from "@util/logger";
import {VsanFileShareSnapshot} from "@generated/vsan-file-share-snapshot";
import {TaskMonitorService} from "@service/task-monitor-service";
import {Privileges} from "@generated/privileges";
import {PermissionService} from "@generated/permission-service";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";
import {VsanFileShareSnapshotNameComparator} from "../comparators/snapshot-name-comparator";
import {PipeComparator} from "@component/datagrid/comparator/pipe-comparator";
import {VsanDateFormatterPipe} from "@pipe/VsanDateFormatterPipe";
import {PipeFilter} from "@component/datagrid/filter/pipe-filter";
import {FileSizePipe} from "@pipe/FileSizePipe";
import {NumericComparator} from "@component/datagrid/comparator/numeric-comparator";
import {VsanFileShareSnapshotQueryResult} from "@generated/vsan-file-share-snapshot-query-result";
import {ModalBuilder} from "@util/modal-builder";
import {FileServiceDeleteSnapshotModalContext} from "./delete-snapshot-modal-context.data";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {FileServiceCreateSnapshotModalContext} from "./create-snapshot-modal-context.data";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "vsan-file-service-snapshots",
   templateUrl: "snapshot-list.html",
   styleUrls: ["snapshot-list.scss"],
})
export class SnapshotListComponent implements OnInit {
   private readonly MAX_SNAPSHOT: number = 32;
   private readonly NAME_FIELD: string = "config.name";
   private readonly CAPACITY_FIELD: string = "usedCapacity";
   private readonly CREATION_TIME_FIELD: string = "creationTime";

   private _share: VsanFileServiceShare;

   @Input()
   set share(val: VsanFileServiceShare) {
      this._share = val;
      this.fetch();
   }

   get share(): VsanFileServiceShare {
      return this._share;
   }

   totalSnapshots: number;
   snapshots: VsanFileShareSnapshot[];
   selectedSnapshots: VsanFileShareSnapshot[] = [];

   loading: boolean;
   error: string;
   hasEditPermission: boolean;

   nameFilter: ObjectFilter<any> = new ObjectFilter<any>(this.NAME_FIELD);
   nameComparator: VsanFileShareSnapshotNameComparator = new VsanFileShareSnapshotNameComparator();

   capacityFilter: PipeFilter<any> = new PipeFilter<any>(new FileSizePipe(), this.CAPACITY_FIELD);
   capacityComparator: NumericComparator<any> = new NumericComparator<any>(this.CAPACITY_FIELD);

   creationTimeFilter: PipeFilter<any> = new PipeFilter<any>(new VsanDateFormatterPipe(), this.CREATION_TIME_FIELD);
   creationTimeComparator: PipeComparator<any> =
         new PipeComparator<any>(new VsanDateFormatterPipe(), this.CREATION_TIME_FIELD);

   constructor(private fileServiceConfigService: VsanFileServiceConfigService,
         private snapshotService: VsanFileServiceSnapshotService,
         private permissionService: PermissionService,
         private taskMonitor: TaskMonitorService) {}

   async ngOnInit(): Promise<void> {
      try {
         this.hasEditPermission =
               await this.permissionService.hasPermissions(ManagedObject.contextObject, [Privileges.CONFIG_STORAGE()]);
      } catch (err) {
         Logger.error(err);
      }
   }

   private async fetch(): Promise<void> {
      const spec: VsanFileShareSnapshotQuerySpec = VsanFileShareSnapshotQuerySpec.Factory.create(
            this.share.uuid,
            1,
      );
      try {
         this.loading = true;
         const result: VsanFileShareSnapshotQueryResult =
               await this.snapshotService.queryShareSnapshots(ManagedObject.contextObject, spec);
         this.snapshots = result.snapshots;
         this.totalSnapshots = result.total;
      } catch (err) {
         Logger.error(err);
         this.error = err;
      } finally {
         this.loading = false;
      }
   }

   onAdd = () => {
      const context: FileServiceCreateSnapshotModalContext = {
         share: this.share,
         existingSnapshots: this.snapshots.map(snapshot => snapshot.config.name),
      } as FileServiceCreateSnapshotModalContext;
      new ModalBuilder("fileServiceShares/createSnapshot")
            .setDimensions([600, 250])
            .open(context)
            .then(this.onSuccess);
   };

   onDelete = () => {
      const context: FileServiceDeleteSnapshotModalContext = {
         share: this.share,
         snapshots: this.selectedSnapshots,
      } as FileServiceDeleteSnapshotModalContext;
      const dialogHeight: number = this.selectedSnapshots.length === 1 ? 250 : 350;
      new ModalBuilder("fileServiceShares/deleteSnapshot")
            .setDimensions([600, dialogHeight])
            .open(context)
            .then(this.onSuccess);
   };

   private onSuccess = async (taskRef: ManagedObject | ManagedObject[]): Promise<void> => {
      await this.awaitTask(taskRef);
      this.fetch();
   };

   private async awaitTask(taskRef: ManagedObject | ManagedObject[]): Promise<void> {
      if (!taskRef) {
         return;
      }
      try {
         if (Array.isArray(taskRef)) {
            await Promise.all(taskRef.map(task => this.taskMonitor.getAwaitResult(task)));
         } else {
            await this.taskMonitor.getAwaitResult(taskRef);
         }
      } catch (err) {
         this.error = err;
      }
   }

   get addSnapshotAvailable(): boolean {
      return this.hasEditPermission && this.totalSnapshots < this.MAX_SNAPSHOT;
   }

   get deleteSnapshotEnabled(): boolean {
      // The latest snapshot cannot be deleted due to backend limitation
      // UI should disable the delete button if user selects the latest snapshot
      return this.hasEditPermission
            && !this.selectedSnapshots
                  .some(snapshot => this.snapshots[this.snapshots.length - 1].config.name === snapshot.config.name);
   }

   get addButtonTitle(): string {
      if (this.addSnapshotAvailable) {
         return VsanUiUtils.getString("vsan.fileservices.shares.grid.details.tab.snapshot.create");
      } else if (!this.hasEditPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      return VsanUiUtils.getString("vsan.fileservices.shares.grid.details.tab.snapshot.create.max");
   }

   get deleteButtonTitle(): string {
      if (this.deleteSnapshotEnabled) {
         return VsanUiUtils.getString("vsan.fileservices.shares.grid.details.tab.snapshot.delete.title");
      }
      return VsanUiUtils.getString("vsan.fileservices.shares.grid.details.tab.snapshot.delete.disable.title");
   }
}
