import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {FileSharePerfData} from "@component/file-share-perf/file-share-perf-data";
import {VsanPerfPropertyProvider} from "@generated/vsan-perf-property-provider";
import {PerfMonitorCommonPropsData} from "@generated/perf-monitor-common-props-data";
import {ManagedObject} from "@service/managed-object";
import {VolumePerfData} from "@component/file-share-perf/volume-perf-data";

@Component({
   selector: "file-share-performance-details",
   templateUrl: "share-performance-details.html",
   styleUrls: ["share-performance-details.scss"],
})
export class SharePerformanceDetailsComponent {

   @Input()
   share: VsanFileServiceShare;

   @Input()
   smbProtocolSupported: boolean;

   @Input()
   smbProtocolLayerPerformanceSupported: boolean;

   @Input()
   clusterRef: ManagedObject;

   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   volumePerfData: VolumePerfData;

   constructor(private perfProvider: VsanPerfPropertyProvider) {}

   async ngOnChanges(): Promise<void> {
      if (!this.share) {
         return;
      }
      await this.queryPerformanceData();
   }

   async queryPerformanceData(): Promise<void> {
      try {
         this.volumePerfData = null;
         const commonPerfData: PerfMonitorCommonPropsData = await this.perfProvider.getPerfMonitorCommonPropsData(
               ManagedObject.contextObject);

         const fileSharePerfData: FileSharePerfData = new FileSharePerfData(this.share, this.smbProtocolSupported,
               this.smbProtocolLayerPerformanceSupported);
         this.volumePerfData = new VolumePerfData(commonPerfData, fileSharePerfData);
      } catch (err) {
         this.onError.emit(err);
      }
   }

}
