import {VsanFileShareUtils} from "../file-share-util";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileShareOwnerType} from "@generated/vsan-file-share-owner-type";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {ObjectFilter} from "@component/datagrid/filter/object-filter";

/**
 * Filter class for File share owner type column
 */
export class OwnerTypeFilter extends ObjectFilter<VsanFileServiceShare> {
   constructor() {
      super(VsanFileShareUtils.OWNER_TYPE_FIELD);
   }

   protected stringify(data: VsanFileServiceShare): string  {
      if (!data) {
         return "";
      }
      const ownerType: string = VsanUiUtils.stringify(data, this.dataField);
      return VsanFileShareUtils.getShareOwnerType(ownerType as VsanFileShareOwnerType);
   }
}
