import {Injectable} from "@angular/core";
import {FileShareLoader} from "./file-share-loader";
import {ManagedObject} from "@service/managed-object";
import {FileShareLegacyLoader} from "./file-share-legacy-loader";
import {VsanFileSharesService} from "@generated/vsan-file-shares-service";

@Injectable()
export class FileShareLoaderFactory {
   constructor(private shareService: VsanFileSharesService) {}

   createLoader(isPaginationSupported: boolean, domainName: string): FileShareLoader {
      if (isPaginationSupported) {
         return new FileShareLoader(domainName, ManagedObject.contextObject, this.shareService);
      }
      return new FileShareLegacyLoader(domainName, ManagedObject.contextObject, this.shareService);
   }
}
