import {BaseComparator} from "@component/datagrid/comparator/base-comparator";
import {VsanFileShareUtils} from "../file-share-util";
import {VsanFileShareProtocol} from "@generated/vsan-file-share-protocol";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";

/**
 * Comparator for file share protocol
 */
export class VsanFileShareProtocolComparator extends BaseComparator<VsanFileServiceShare> {
   protected compareValue(a: VsanFileServiceShare, b: VsanFileServiceShare): number {
      const dataField: string = VsanFileShareUtils.PROTOCOL_FIELD;
      const protocolText1: string = this.stringify(a, dataField);
      const protocolText2: string = this.stringify(b, dataField);
      const protocol1: string = VsanFileShareUtils.getShareProtocolLabel(VsanFileShareProtocol[protocolText1]);
      const protocol2: string = VsanFileShareUtils.getShareProtocolLabel(VsanFileShareProtocol[protocolText2]);
      return protocol1.localeCompare(protocol2);
   }
}
