import {Component, EventEmitter, Input, OnChanges, Output} from "@angular/core";
import {VirtualObjectsTypeUtil} from "@component/virtual-objects-table/virtual-objects-type.util";
import {DisplayObjectType} from "@generated/display-object-type";
import {PhysicalPlacementService} from "@generated/physical-placement-service";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VirtualObjectPlacementModel} from "@generated/virtual-object-placement-model";
import {VirtualObjectType} from "@generated/virtual-object-type";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanFileShareOwnerType} from "@generated/vsan-file-share-owner-type";
import {VirtualObjectsContext} from "@navigation/context/virtual-objects-context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ManagedObject} from "@service/managed-object";
import {MapUtil} from "@util/map-util";
import {VsanFileShareUtils} from "../file-share-util";

@Component({
   selector: "file-share-placement-details",
   templateUrl: "share-placement-details.html",
   styleUrls: ["share-placement-details.scss"],
})
export class SharePlacementDetailsComponent implements OnChanges {

   @Input()
   share: VsanFileServiceShare;

   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   // We use `Map<string, VirtualObjectPlacementModel[]>` because a share has several vSAN objects,
   // as return type of `VirtualObjectsService.getVsanPhysicalPlacement`, The key here is vsanObjectUuid.
   // We display the share detail view in shares list, user can navigate between shares.
   // Considering the physical placement data is not changed frequently, we want to cache the query result,
   // so UI used a Map of Map, the key of this Map is shareUuid.
   placementData: Map<string, Map<string, VirtualObjectPlacementModel[]>> =
         new Map<string, Map<string, VirtualObjectPlacementModel[]>>();

   constructor(private navigationService: NavigationService,
         private physicalPlacementService: PhysicalPlacementService) {
   }

   ngOnChanges(): Promise<void> {
      if (!this.share) {
         return;
      }
      this.queryPlacementData();
   }

   private async queryPlacementData(): Promise<void> {
      if (this.placementData.get(this.share.uuid)) {
         return;
      }
      try {
         const placement: any = await this.physicalPlacementService.getVsanPhysicalPlacement(
               ManagedObject.contextObject,
               this.share.objectUuids);
         this.placementData.set(this.share.uuid, MapUtil.toMap<string, VirtualObjectPlacementModel[]>(placement));
      } catch (err) {
         this.onError.emit(err);
      }
   }

   getVirtualObjects(): VirtualObjectModel[] {
      if (!this.placementData.get(this.share.uuid)) {
         return null;
      }

      return this.share.objectUuids.map(objectUuid => {
         return {
            uid: objectUuid,
            name: this.share.config.name,
            iconId: VsanFileShareUtils.getShareIcon(this.share),
         } as VirtualObjectModel;
      });
   }

   navigateToVirtualObjects(): void {
      const virtualObjectsContext: VirtualObjectsContext = {
         presentVirtualObjectUuids: this.share.objectUuids,
         filterTypes: [this.getFilterObjectsTypeFilter()],
      };
      this.navigationService.navigateTo(PlatformExtension.VIRTUAL_OBJECTS,
            ManagedObject.contextObject,
            virtualObjectsContext);
   }

   private getFilterObjectsTypeFilter(): VirtualObjectType {
      return this.share.managedBy === VsanFileShareOwnerType.CNS
            ? VirtualObjectsTypeUtil.getVirtualObjectTypeFilter(DisplayObjectType.FILE_VOLUME)
            : VirtualObjectsTypeUtil.getVirtualObjectTypeFilter(DisplayObjectType.FILE_SHARE);
   }
}
