import {BaseComparator} from "@component/datagrid/comparator/base-comparator";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";

/**
 * Comparator for file share usage
 */
export class VsanFileShareUsageComparator extends BaseComparator<VsanFileServiceShare> {
   constructor(private dataField: string) {
      super();
   }

   protected compareValue(a: VsanFileServiceShare, b: VsanFileServiceShare): number {
      const value1: string = this.stringify(a, this.dataField);
      const value2: string = this.stringify(b, this.dataField);
      if (!isNaN(parseInt(value1)) && !isNaN(parseInt(value2))) {
         return parseInt(value1) - parseInt(value2);
      } else if (!isNaN(parseInt(value1))) {
         return 1;
      } else if (!isNaN(parseInt(value2))) {
         return -1;
      }
      return 0;
   }
}
