import {Component, Input} from "@angular/core";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanFileShareProtocol} from "@generated/vsan-file-share-protocol";
import {VsanFileShareUtils} from "../file-share-util";
import {VsanFileShareNfsSecurityType} from "@generated/vsan-file-share-nfs-security-type";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {VsanUiUtils} from "@util/vsan-util";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {AffinitySiteLocation} from "@generated/affinity-site-location";
import {FileServiceUtil} from "../../file-service-util";
import {ServiceUtils} from "../../../services/service.utils";
import {DomainOrHostData} from "@generated/domain-or-host-data";

@Component({
   selector: "file-share-basic-details",
   templateUrl: "share-basic-details.html",
   styleUrls: ["share-basic-details.scss"],
})
export class ShareBasicDetailsComponent {
   VsanFileShareProtocol: typeof VsanFileShareProtocol = VsanFileShareProtocol;
   VsanFileShareUtils: typeof VsanFileShareUtils = VsanFileShareUtils;
   VsanFileShareNfsSecurityType: typeof VsanFileShareNfsSecurityType = VsanFileShareNfsSecurityType;

   @Input()
   share: VsanFileServiceShare;

   @Input()
   policies: StoragePolicyData[];

   @Input()
   hostPropertiesMap: Map<string, any>;

   @Input()
   availableDomains: DomainOrHostData[];

   @Input()
   affinitySiteAvailable: boolean;

   @Input()
   smbAbeSupported: boolean;

   @Input()
   isFileAnalyticsEnabledOnCluster: boolean;

   constructor(private navigationService: NavigationService) {}

   getPolicyName(): string {
      let policy: StoragePolicyData;
      if (this.policies) {
         policy = this.policies.find(policyItem => policyItem.id === this.share.config.policyId);
      }
      return policy ? policy.name : null;
   }

   getLocalizedNumber(number: number): string {
      return VsanUiUtils.localizeNumber(number);
   }

   getAffinitySite(site: AffinitySiteLocation): string {
      return FileServiceUtil.getAffinitySiteLabel(site, this.availableDomains);
   }

   getFileAnalyticsStatus(): string {
      return ServiceUtils.enabledStateLabel(this.share.config.isFileAnalyticsEnabled);
   }

   navigateToHost(): void {
      this.navigationService.navigateTo(
            PlatformExtension.HOST_SUMMARY,
            this.hostPropertiesMap.get(this.share.host).nodeRef);
   }
}
