import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileShareSnapshot} from "@generated/vsan-file-share-snapshot";
import {FileServiceDeleteSnapshotModalContext} from "./delete-snapshot-modal-context.data";
import {VsanFileServiceSnapshotService} from "@generated/vsan-file-service-snapshot-service";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {FileSizePipe} from "@pipe/FileSizePipe";
import {ShareSnapshotRemoveResult} from "@generated/share-snapshot-remove-result";

@Component({
   templateUrl: "delete-snapshot.html",
   selector: "vsan-file-service-delete-snapshot",
   styleUrls: ["delete-snapshot.scss"],
   providers: [FileSizePipe],
})
export class DeleteSnapshotComponent implements OnInit {
   clusterRef: ManagedObject;
   snapshots: VsanFileShareSnapshot[];
   share: VsanFileServiceShare;
   errors: string[];
   totalReleasedCapacity: string;

   constructor(private snapshotService: VsanFileServiceSnapshotService, private fileSizePipe: FileSizePipe) {
   }

   ngOnInit(): void {
      const context: FileServiceDeleteSnapshotModalContext =
            VsanUiUtils.getModalContext() as FileServiceDeleteSnapshotModalContext;
      this.snapshots = context.snapshots;
      this.share = context.share;
      this.clusterRef = ManagedObject.contextObject;

      const capacity: number = this.snapshots.reduce((sum, snapshot) => sum += snapshot.usedCapacity, 0);
      this.totalReleasedCapacity = this.fileSizePipe.transform(capacity);
   }

   async confirm(): Promise<void> {
      this.errors = null;
      const snapshotNames: string[] = this.snapshots.map(snapshot => snapshot.config.name);
      try {
         const result: ShareSnapshotRemoveResult =
               await this.snapshotService.removeShareSnapshot(this.clusterRef, this.share.uuid, snapshotNames);
         if (VsanUiUtils.isEmpty(result.errors)) {
            VsanUiUtils.closeModalDialog(result.taskRefs);
         } else {
            this.errors = [VsanUiUtils.getString("vsan.fileservices.snapshot.delete.dialog.error")];
         }
      } catch (err) {
         this.errors = [err];
      }
   }

   get dialogTitle(): string {
      if (this.snapshots.length === 1) {
         return VsanUiUtils.getString("vsan.fileservices.snapshot.delete.dialog.title.single");
      }
      return VsanUiUtils.getString("vsan.fileservices.snapshot.delete.dialog.title.plural");
   }

   getDialogDescText(): string {
      if (!VsanUiUtils.isEmpty(this.snapshots) && this.snapshots.length === 1) {
         return VsanUiUtils.getString("vsan.fileservices.snapshot.delete.dialog.desc.single",
               this.snapshots[0].config.name,
               this.share.config.name,
               this.totalReleasedCapacity);
      }
      return VsanUiUtils.getString("vsan.fileservices.snapshot.delete.dialog.desc.plural",
            this.share.config.name,
            this.totalReleasedCapacity);
   }

   getSummaryDescText(): string {
      if (this.snapshots.length === 1) {
         return VsanUiUtils.getString("vsan.fileservices.snapshot.delete.dialog.summarize.single");
      }
      return VsanUiUtils.getString("vsan.fileservices.snapshot.delete.dialog.summarize.plural", this.snapshots.length);
   }
}
