import {BaseComparator} from "@component/datagrid/comparator/base-comparator";
import {VsanFileServiceShareSize} from "@generated/vsan-file-service-share-size";
import {Logger} from "@util/logger";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";

/**
 * Comparator for file share quota/softQuota
 */
export class VsanFileShareQuotaComparator extends BaseComparator<VsanFileServiceShare> {
   constructor(private quotaField: string, private quotaSizeField: string) {
      super();
   }

   protected compareValue(a: VsanFileServiceShare, b: VsanFileServiceShare): number {
      const quota1: number = parseFloat(this.stringify(a, this.quotaField));
      const quotaSize1: VsanFileServiceShareSize = this.stringify(a, this.quotaSizeField) as VsanFileServiceShareSize;

      const quota2: number = parseFloat(this.stringify(b, this.quotaField));
      const quotaSize2: VsanFileServiceShareSize = this.stringify(b, this.quotaSizeField) as VsanFileServiceShareSize;
      return this.parseSize(quota1, quotaSize1) - this.parseSize(quota2, quotaSize2);
   }

   private parseSize(quota: number, unitType: VsanFileServiceShareSize): number {
      if (!quota) {
         return 0;
      }
      switch(unitType) {
         case VsanFileServiceShareSize.MB:
            return quota * Math.pow(1024, 2);
         case VsanFileServiceShareSize.GB:
            return quota * Math.pow(1024, 3);
         case VsanFileServiceShareSize.TB:
            return quota * Math.pow(1024, 4);
         default:
            Logger.warn("Not supported quota size type: ", unitType);
            return null;
      }
   }
}
